* {
  margin: 20px;
}
.app{
  position: relative;
}
.controls{
}
.single-filter{
  float: left;
  margin: 0px;
  width: 200px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.single-filter p {
  text-transform: uppercase;
  margin: 0;
}
.error-msg{
  margin: 0;
  display: inline-block;
  padding: 10px;
  background: red;
  position: fixed;
  right: 0px;
  -webkit-transition: right .5s ease-out;
  -o-transition: right .5s ease-out;
  transition: right .5s ease-out;
}

.is-notification {
  right: -500px;
}
.is-visible{
  display: none;
}
.preview-height{
  height: 400px;
}
.preview-width{
  width: 200px;
}



